@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #34333a;
  font-family: 'Roboto Condensed', sans-serif;
  color: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
}

button {
  background-color: transparent;
  color: #fafafa;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 50%;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.2);
  font-size: 20px;
  padding: 10px 15px;
  margin: 10px;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.timer-container {
  margin: -50px 20px 20px;
}

.timer-container h2 {
  margin:  0;
}

.actions-wrapper span {
  font-size: 40px;
  margin: 0 10px;
  width: 40px;
}

.clock-container {
  border: 1px solid #fafafa;
  border-radius: 10px;
  padding: 10px;
  display: inline-block;
  margin-top: 20px;
  width: 300px;
}

.clock-container h1 {
  margin: 0;
}

.clock-container span {
  font-size: 50px;
}